import { tns } from "tiny-slider";



const slider = () => {
    try {
        const prev = document.querySelector('.slider__arrow_prev');
        const next = document.querySelector('.slider__arrow_next')

        function goToSlide(directionArrow) {
            directionArrow.addEventListener('click', () => {
                if (directionArrow.classList.contains('slider__arrow_prev')) {
                    reviewsSlider.goTo('prev');
                } else if (directionArrow.classList.contains('slider__arrow_next')) {
                    reviewsSlider.goTo('next');
                } else {
                    console.log(directionArrow.className);
                }
            })
        }

        goToSlide(prev);
        goToSlide(next);
        
        if (window.innerWidth <= 768) {
            prev.classList.add('mobile');
            next.classList.add('mobile');
        }

        const reviewsSlider = tns({
            container: '.slider__inner',
            items: 1,
            slideBy: 'page',
            
            controls: false,

            swipeAngle: true,
            mouseDrag: true,
            
            speed: 600,
            autoplay: true,
            autoplayButtonOutput: false,
            
            autoWidth: true,
            gutter: 20,
            center: true,
        });
    } catch (error) {
    }   
};

export default slider;