'use strict';

const descrItem = () => {
    try {
        const descrBtn = document.querySelector('.catalog__btn-descr');
        const backBtn = document.querySelector('.catalog__btn-back');


        descrBtn.addEventListener('click', (e) => {
            e.preventDefault();
            const itemContainer = e.currentTarget.parentNode.parentNode; 
            itemContainer.style.transform = 'translateX(-50%)';
        });

        backBtn.addEventListener('click', (e) => {
            e.preventDefault();
            const itemContainer = e.currentTarget.parentNode.parentNode; 
            itemContainer.style.transform = 'translateX(0)';
        })

    } catch (error) {
    }
};

export default descrItem;