'use strict';

const filter = () => {
    try {
        const itemsParentArr = document.querySelector('.catalog__grid');
        const items = [...document.querySelectorAll('.catalog__item')];


        const itemNames = [...document.querySelectorAll('.catalog__item-sideA .catalog__item-title')];
        const itemPrices = [...document.querySelectorAll('.catalog__item-price .price')];

        const categorySelect = document.querySelector('.filter__category');
        const sortingSelect = document.querySelector('.filter__sorting');
        const searchInput = document.querySelector('.filter__search input');
        const searchBtn = document.querySelector('.filter__search button');
        let term = '';
        
        function setDataAttributesForItems() {
            items.forEach(item => item.classList.add('catalog__item_active'));
            itemNames.forEach((name, i) => {
                items[i].setAttribute('data-name', name.textContent);
            });
            itemPrices.forEach((price, i) => {
                items[i].setAttribute('data-price', price.textContent);
            })
        }

        function sortByPrice(arr, sortingProp = 'toUp') {
            arr.slice().sort(function (a, b) {
                const numbA = +a.querySelector('.price').textContent;
                const numbB = +b.querySelector('.price').textContent;
    
                if (sortingProp === 'toLow') {
                    return numbB - numbA;
                }
                if (sortingProp === 'toUp') {
                    return numbA - numbB;
                } 
            }).forEach(item => itemsParentArr.appendChild(item));
        }

        function sortByName(arr, sortingProp = 'noReverse') {
            const resArr = [...arr];
            resArr.slice().sort(function (a, b) {
                const nameA = a.querySelector('.catalog__item-title').textContent;
                const nameB = b.querySelector('.catalog__item-title').textContent;

                if (sortingProp === 'noReverse') {
                    if (nameA > nameB) {
                        return 1;
                    }
                    if (nameA < nameB) {
                        return -1;
                    }
                    0
                }

                if (sortingProp === 'reverse') {
                    if (nameA > nameB) {
                        return -1;
                    }
                    if (nameA < nameB) {
                        return 1;
                    }
                    0
                }        
            }).forEach(item => itemsParentArr.appendChild(item));
        }

        function search(arr, term) {
            if (term === '') {
                return arr;
            } 
    
            return arr.filter(item => {
                const itemName = item.querySelector('.catalog__item-title').textContent;
                return itemName.indexOf(term) > -1;    
            })
        }

        setDataAttributesForItems();
  
        searchInput.addEventListener('change', (e) => {
            term = e.target.value;
        })

        searchBtn.addEventListener('click', (e) => {    
            const someArr = search(items, term);
            itemsParentArr.innerHTML = '';
            someArr.forEach(item => itemsParentArr.appendChild(item));
        })

       
        categorySelect.addEventListener('change', (e) => {
            items.forEach(item => {
                if (e.target.value === item.getAttribute('data-category')) {
                    item.classList.add('catalog__item_active');
                } else if (e.target.value === 'Без категории') {
                    item.classList.add('catalog__item_active');
                } else {
                    item.classList.remove('catalog__item_active');                    
                }
            })
        });   
        

        sortingSelect.addEventListener('change', (e) => {
            switch (e.target.value) {
                case '':
                    console.log(itemsParentArr);
                    return items;
                case 'Порядок: по умолчанию':
                    console.log(itemsParentArr);
                    return items.forEach(item => itemsParentArr.appendChild(item));
                case 'Цена: по возрастанию': 
                    return sortByPrice(items, 'toUp');
                case 'Цена: по убыванию': 
                    return sortByPrice(items, 'toLow');
                case 'Название: А—Я': 
                    return sortByName(items, 'noReverse');
                case 'Название: Я—А': 
                    return sortByName(items, 'reverse');
                case 'Порядок: сначала новые': 
                    return sortByName(items, 'noReverse');
                case 'Порядок: сначала старые': 
                    return sortByName(items, 'reverse');
                default:
                    console.log('Нет совпадений')
            }
        });


    } catch (error) {
    }
};

export default filter;